import React from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import calculateSpacing from './calculateSpacing'
import {
    Grid,
    Typography,
    isWidthUp,
    withWidth,
    withStyles
} from "@material-ui/core";
import ZoomImage from "../../../shared/components/ZoomImage";

const styles = theme => ({
    containerFix: {
        maxHeight:"25vh",
        [theme.breakpoints.down("md")]: {
            paddingLeft: theme.spacing(6),
            paddingRight: theme.spacing(6)
        },
        [theme.breakpoints.down("sm")]: {
            paddingLeft: theme.spacing(4),
            paddingRight: theme.spacing(4)
        },
        [theme.breakpoints.down("xs")]: {
            paddingLeft: theme.spacing(2),
            paddingRight: theme.spacing(2)
        },
        overflow: "hidden",
        paddingTop: theme.spacing(1),
        paddingBottom: theme.spacing(1)
    },
    cardWrapper: {
        [theme.breakpoints.down("xs")]: {
            marginLeft: "auto",
            marginRight: "auto",
            maxWidth: 340
        }
    },
    cardWrapperHighlighted: {
        [theme.breakpoints.down("xs")]: {
            marginLeft: "auto",
            marginRight: "auto",
            maxWidth: 360
        }
    },
    image: {
        width: "90%",
        verticalAlign: "middle",
        //height: '70vh',
        // objectFit: 'cover',
        borderRadius: theme.shape.borderRadius,
        boxShadow: theme.shadows[4],
       // opacity: '0.85',
    },
    image2: {
        width: "90%",
        verticalAlign: "middle",
        height: '50%',
        // objectFit: 'cover',
        borderRadius: theme.shape.borderRadius,
        boxShadow: theme.shadows[4],
       // opacity: '0.85',
    },
});

function Partenaires(props) {
    const { width, classes } = props;
    const image1 = `${process.env.PUBLIC_URL}/images/logged_out/part1.png`
    const image2 = `${process.env.PUBLIC_URL}/images/logged_out/part2.png`
    return (
        <div className="lg-small-top" style={{ backgroundColor: "#FFFFFF" }}>
            <h3 style={{
                color: '#0c2e60',
                fontFamily: 'Poppins,sans-serif',
                fontWeight: 600,
                fontSize: '4vh',
                textAlign: 'center'
            }
            }>
                Nos partenaires
            </h3>
            <div className={classNames("container-fluid", classes.containerFix)}>
                <Grid
                    container
                    spacing={calculateSpacing(width)}
                    className={classes.gridContainer}
                >
                    <Grid

                        item
                        xs={12}
                        sm={6}
                        lg={6}
                        className={classes.cardWrapper}
                        data-aos="zoom-in-up"
                    >
                        <ZoomImage
                            src={image1}
                            alt="header example"
                            className={classes.image}
                        />
                    </Grid>
                    <Grid

                        item
                        xs={12}
                        sm={6}
                        lg={6}
                        className={classes.cardWrapper}
                        data-aos="zoom-in-up"
                    >
                        <ZoomImage
                            src={image2}
                            alt="header example"
                            className={classes.image2}
                        />
                    </Grid>



                </Grid>
            </div>
        </div>
    );
}

Partenaires.propTypes = {
    width: PropTypes.string.isRequired
};

export default withStyles(styles, { withTheme: true })(
    withWidth()(Partenaires)
);
