import React from 'react';
import {Grid,makeStyles,Avatar} from '@material-ui/core'
import MenuBookIcon from '@material-ui/icons/MenuBook';
import { faUserGraduate,faChalkboardTeacher } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
const items =[
    {
        icone:<MenuBookIcon style={{fontSize:'8vh',color:"#b3294e"}}></MenuBookIcon> ,
        text:'Modules de formation'

    },
    {
        icone:<FontAwesomeIcon style={{fontSize:'5vh',color:"#b3294e"}} icon={faUserGraduate}  ></FontAwesomeIcon> ,
        text:'Participants '

    },
    {
        icone:<FontAwesomeIcon style={{fontSize:'6vh',color:"#b3294e"}} icon={faChalkboardTeacher}  ></FontAwesomeIcon>  ,
        text:'Formateurs Experts'

    }
]

function Infos(props) {
    return (
        <div style={{margin:"auto", width:"100%"}}>
              <Grid container spacing={1}>
                  
                    {   items.map((el,index)=>(

                        <Grid item key={index}  md={4} style={{display:"flex",flexDirection:"row", justifyContent:"center",alignItems:"center",paddingLeft:20}}>
                        <Avatar style={{width:"12vh",height:"12vh",backgroundColor:"white"}} >
                            {el.icone}
                        </Avatar>
                        <div style={{display:"flex",flexDirection:"column", justifyContent:"center",marginLeft:"4%"}}>
                        <span style={{color:'white', fontSize:18}}>{el.text}</span>
                        </div>
                        </Grid>
                    ))      
                    }
              </Grid>
            
        </div>
    );
}

export default Infos;