import React from 'react';
import { Grid, Typography, makeStyles } from "@material-ui/core";
import ModuleDescription from './ModuleDescription'
import { Modules } from '../../dummy_data/cours';

function CoursDetails(props) {
    const classes = styles();
    return (
        <div className={"container-fluid"}>
            <div className={classes.image + " " + "lg-small-top"}>
                <div className={classes.divTitle}>
                    <Typography  gutterBottom variant="h1"  component="h2" style={{ color:"white", textAlign: "center"}}>
                        {"Description des modules"}
                    </Typography>

                </div>
            </div>
            <div className={"lg-small-top"}>
                {
                    <Grid
                        container
                        spacing={2}
                        className={classes.gridContainer}
                    >
                        {
                            Modules.map((m,index) => {
                                return (
                                    <Grid
                                        item
                                        md={6}
                                        key={index}
                                    >
                                        <ModuleDescription module={m} />
                                    </Grid>)
                            })
                        }

                    </Grid>

                }
            </div>

        </div>
    );
}
const styles = makeStyles((theme) => ({
    image: {
        backgroundImage: `url(${process.env.PUBLIC_URL}/images/logged_out/courses.jpg)`,
        backgroundRepeat: 'no-repeat',
        backgroundColor:
            theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        height: '45vh',
        "&::before": {
            content: "''",
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: ' rgba(0,0,0,.75)',
            height: '45%',
            width: '100%',
        }
    },
    divTitle: {
        position: 'relative',
        marginTop: '4%',
    },
    title: {
        color: 'white',
      //  marginLeft: "10%",
      //  fontSize: 60,
        marginBottom: 0
    },
    subtitle: {
        color: '#b3294e',
        marginLeft: "10%",
        fontSize: 30,
    },
    video: {
        width: 'calc(100%)',
        maxWidth: '100%',
        outline: 'none',
    },
    button: {
        left: '45%',
        height: '7vh',
        position: 'relative',
        width: '25vh',
        left: "2%",
        //top:"46%",
        backgroundColor: "#b3294e"
    },
    spanText: {
        color: '#b3294e',
        fontWeight: '500'

    },
    divDescription: {
        border: '1px solid #e0e4e6',
        borderRadius: '3px',
        padding: '25px',
        //position: relative;
        marginBottom: '20px'
    }


}));

export default CoursDetails;