import axiosinstance from './instance';
export const getModules = ()=> {
    return axiosinstance.get('api/participant/module');
}
export const getQuizByModule = (id)=> {
    return axiosinstance.get('api/participant/module/'+id+'/quiz');
}
export const getModuleById = (id)=> {
    return axiosinstance.get('api/module/'+id);
}
export const sendScore = (data)=> {
    return axiosinstance.post('api/participant/quiz',data);
}
export const getQuestionnaire = (id)=> {
    return axiosinstance.get('api/participant/module/'+id+'/evalution');
}

export const getFormateurModules = ()=> {
    return axiosinstance.get('api/formateur/module');
}
export const makeModuleLive = (id)=>{
    return axiosinstance.get('api/formateur/module/'+id+'/onlive');
}

export const getStatQuiz = (id)=>{
    return axiosinstance.get('api/stats/score/module/'+id);
}


export const sendEvaluation = (data)=>{
    return axiosinstance.post('api/participant/evaluation',data);
}
export const getEvaluationState = (id)=>{
    return axiosinstance.get('api/stats/evalution/module/'+id)
}
export const ChangePwd = (data)=>{
    return axiosinstance.post('api/changepassword',data)
}