import React from 'react';
import {
    Grid,
    Paper
} from "@material-ui/core";

function ModuleDescription({ module }) {
    return (
        <div>

            <Paper elevation={3} style={{
                padding: '3%',
                //cursor: 'pointer',
               // height: '83vh'
               overflow:'auto',
               minHeight:"556px"
            }} >
                <div style={{ //height: '25vh'
                 }}>
                    <h3 style={{ fontSize: '17px', color: '#b3294e', textAlign: 'center' }}>{module.slug}</h3>
                     <div style={{display:'flex',flexWrap:"wrap", alignItems:'center'}}>
                     <span style={{ fontFamily: 'Marianne,Arial,sans-serif',fontWeight: 700, color: '#4829B2', width:"30%" }}> Thme en arabe:</span>
                     <p style={{ fontFamily: 'Marianne,Arial,sans-serif'  ,  width:"70%" ,textAlign: 'justify',
  textJustify: 'inter-word',fontWeight:'600'  }}>{module.titreAr}</p>
                     </div>

                     <div style={{display:'flex',flexWrap:"wrap", alignItems:'center'}}>
                     <span style={{ fontFamily: 'Marianne,Arial,sans-serif',fontWeight: 700, color: '#4829B2', width:"30%" }}> Thme en franais:</span>
                     <p style={{ fontFamily: 'Marianne,Arial,sans-serif'  ,    width:"70%" ,textAlign: 'justify',
  textJustify: 'inter-word',fontWeight:'600'  }}>{module.titre}</p>
                     </div>

                     <div style={{display:'flex',flexWrap:"wrap", alignItems:'center'}}>
                     <span style={{ fontFamily: 'Marianne,Arial,sans-serif',fontWeight: 700, color: '#4829B2', width:"30%" }}> Objectifs:</span>
                     <p style={{ fontFamily: 'Marianne,Arial,sans-serif'  ,  width:"70%" ,textAlign: 'justify',
  textJustify: 'inter-word',fontWeight:'600'  }}>{module.objectif}</p>
                     </div>

                     <div style={{display:'flex',flexWrap:"wrap", alignItems:'center'}}>
                     <span style={{ fontFamily: 'Marianne,Arial,sans-serif',fontWeight: 700, color: '#4829B2', width:"30%" }}> Public cibl:</span>
                     <p style={{ fontFamily: 'Marianne,Arial,sans-serif'  ,  width:"70%" ,textAlign: 'justify',
  textJustify: 'inter-word',fontWeight:'600'  }}>{module.public}</p>
                     </div>

                     <div style={{display:'flex',flexWrap:"wrap", alignItems:'center'}}>
                     <span style={{ fontFamily: 'Marianne,Arial,sans-serif',fontWeight: 700, color: '#4829B2', width:"30%" }}> Nombre de participants:</span>
                     <p style={{ fontFamily: 'Marianne,Arial,sans-serif'  ,  width:"70%" ,textAlign: 'justify',
  textJustify: 'inter-word',fontWeight:'600' }}>{module.nombre}</p>
                     </div>


                     <div style={{display:'flex',flexWrap:"wrap", alignItems:'center'}}>
                     <span style={{ fontFamily: 'Marianne,Arial,sans-serif',fontWeight: 700, color: '#4829B2', width:"30%" }}> Formateur:</span>
                     <p style={{ fontFamily: 'Marianne,Arial,sans-serif'  ,  width:"70%" ,textAlign: 'justify',
  textJustify: 'inter-word',fontWeight:'600'  }}>{module.formateur}</p>
                     </div>

                     <div style={{display:'flex',flexWrap:"wrap", alignItems:'center'}}>
                     <span style={{ fontFamily: 'Marianne,Arial,sans-serif',fontWeight: 700, color: '#4829B2', width:"30%" }}> Date:</span>
                     <p style={{ fontFamily: 'Marianne,Arial,sans-serif'  ,  width:"70%" ,textAlign: 'justify',
  textJustify: 'inter-word' ,fontWeight:'600' }}>{module.date}</p>
                     </div>
                </div>

            </Paper>




        </div >
    );
}

export default ModuleDescription;