import React from "react";
import PropTypes from "prop-types";
import { Grid, Typography, isWidthUp, withWidth } from "@material-ui/core";
import calculateSpacing from "./calculateSpacing";
import ModuleCard from "./ModuleCard";

const iconSize = 30;
const ModulesPart1 =[
  {
    slug : 'MODULE 1',
    titreAr:'    ',
    titre: 'Techniques daccueil, dcoute et dorientation '
  },
  {
    slug : 'MODULE 2',
    titreAr:'          ',
    titre: 'Cadre normatif juridique national en matire de lutte contre la violence faites aux femmes',
  },
  {
    slug : 'MODULE 3',
    titreAr:'           ',
    titre: 'Cadres normatifs juridiques internationaux en matire de lutte contre les violences faites aux femmes'
  },
  
]
  const ModulesPart2 =[
    {
      slug : 'MODULE 4',
      titreAr:'      ',
      titre: 'Rles et responsabilits du directeur de lEMF selon la loi 65-15'
    },
    {
    slug : 'MODULE 5',
    titreAr:'       ',
    titre: 'Formation sur les bases thoriques de cycle de violence FF'
  },
 
]

function FeatureSection(props) {
  const { width } = props;
  return (
    <div style={{ backgroundColor: "#FFFFFF" ,marginTop:'2%' }}>
      <div className="container-fluid">
        <div style={{textAlign:'center',marginBottom:'2%'}}>
{ /*         <span style={{color: '#ef470d',
                      display: 'block',
                      fontSize: '14px',
                      fontWeight: 500,
                      letterSpacing: '2.8px',
                      textTransform: 'uppercase',
                      marginBottom: 10,
  position: 'relative'}}>Top categories</span> */}
        <h3 style={
          {
            color: '#0c2e60',
            fontFamily: 'Poppins,sans-serif',
            fontWeight: 600,
            fontSize:'4vh'
          }

        } >Slectionnez votre module et commencer votre formation </h3>
      </div>

      <div className="container-fluid">
        <Grid container spacing={calculateSpacing(width)}>
          {ModulesPart1.map((element,index) => (
            <Grid
              item
              sm={12}
              md={4}
              key={index}
              data-aos="zoom-in-up"
              data-aos-delay={
                isWidthUp("md", width) ? element.mdDelay : element.smDelay
              }
            >
              <ModuleCard
                slug={element.slug}
                titreAr={element.titreAr}
                titre={element.titre}
              />
            </Grid>
          ))}
           <Grid
              item
              xs={false}
              md={2}
            ></Grid>
           {ModulesPart2.map((element,index) => (
            <Grid
              item
              key={index+3}
              sm={12}
              md={4}
              data-aos="zoom-in-up"
              data-aos-delay={
                isWidthUp("md", width) ? element.mdDelay : element.smDelay
              }
            >
              <ModuleCard
                slug={element.slug}
                titreAr={element.titreAr}
                titre={element.titre}
              />
            </Grid>
          ))}
        </Grid>
      </div>
    </div>
    </div >
  );
}

FeatureSection.propTypes = {
  width: PropTypes.string.isRequired
};

export default withWidth()(FeatureSection);
