import React from 'react';
import Carousel from 'react-material-ui-carousel'
import { Paper, makeStyles } from '@material-ui/core'
import ZoomImage from "../../../shared/components/ZoomImage";
import { createMuiTheme, responsiveFontSizes, ThemeProvider } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

let theme = createMuiTheme();
theme = responsiveFontSizes(theme);

const items =

    [

        { image: `${process.env.PUBLIC_URL}/images/logged_out/stop_violence.jpg` },
        //{ image: `${process.env.PUBLIC_URL}/images/logged_out/stop_violence2.jpg`},
        // { image: `${process.env.PUBLIC_URL}/images/logged_out/home1.png`},
        // { image: `${process.env.PUBLIC_URL}/images/logged_out/home4.jpg`},
    ]


export default function CarouselImages(props) {
    const classes = styles();
    return (
        <Carousel>
            {
                items.map((item, i) =>
                    <div style={{ backgroundColor: "black" }} key={i}>
                        <ZoomImage
                            src={item.image}
                            alt="header example"
                            className={classes.image}
                        />
                        <div className={classes.divHome}>
                            <ThemeProvider theme={theme}>

                                <h1 className={classes.title}>
                                    Formation sur lAppui au renforcement de la lutte contre la violence <br />  lgard des femmes au Maroc et  la disponibilit et la qualit du service
                                </h1>
                                <p className={classes.subTitle}>Mise en uvre du premier cycle de formation  distance  :  Normes de Services essentiels de prise en charge et daccompagnement des Femmes Victimes de Violence </p>

                            </ThemeProvider>
                            {/*              
              <Button
                variant="contained"
                color="secondary"
                className={classes.button}
                startIcon={<SearchIcon />}
              >
                Chercher Un Cours
              </Button>*/}
                        </div>


                    </div>)
            }
        </Carousel>
    )
}

const styles = makeStyles((theme) => ({
    image: {
        width: "90%",
        verticalAlign: "middle",
        height: '70vh',
        // objectFit: 'cover',
        borderRadius: theme.shape.borderRadius,
        boxShadow: theme.shadows[4],
        opacity: '0.85',
        "&::before": {
            content: "''",
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: ' rgba(0,0,0,.75)',
            height: '45%',
            width: '100%',
        }
    },
    subTitle: {
        textAlign: 'center', color: 'white', fontSize: "1.8rem",
        paddingLeft: 10,
        paddingRight: 10
    },
    title: {
        color: "#FFFFFF",
        fontFamily: "'Source Sans Pro',sans-serif",
        fontWeight: 900,
        fontSize: '1.7rem',
        textAlign: 'center',
        //textTransform: 'capitalize',
        textShadow: '2px 2px 4px #000000a1',
    },
    divHome: {
        position: 'absolute',
        top: "40%",
        right:'6%',
        left:'6%'
    },
    "@media (max-width: 1200px)": {
        divHome: {
            top: "20%",
        },
      },
      "@media (max-width: 800px)": {
        divHome: {
            top: "20%",
        },
        title: {
            fontSize: '1.4rem'
        },
        subTitle: {
           fontSize: "1.4rem",
        }
      },
      "@media (max-width: 600px)": {
        divHome: {
            top: "25%",
        },
        title: {
            fontSize: '1rem'
        },
        subTitle: {
           fontSize: "1.4rem",
        }
      },

}));