import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { Typography, withStyles, Paper, Button } from "@material-ui/core";
import { useHistory } from "react-router-dom";

const styles = theme => ({
  iconWrapper: {
    borderRadius: theme.shape.borderRadius,
    textAlign: "center",
    display: "inline-flex",
    alignItems: "center",
    justifyContent: "center",
    marginBottom: theme.spacing(3),
    padding: theme.spacing(1) * 1.5
  }
});


function ModuleCard(props) {
  const { slug, titre, titreAr } = props;
  const history=useHistory();
  return (
    <Fragment>

      <Paper elevation={3} style={{
        textAlign: 'center',
        padding: '5%',
        minHeight:'240px'
        //cursor: 'pointer',
       // height: '38vh'
      }} >
        <div style={{//height:'25vh'
        }}>
          <h3 style={{ fontSize: '1.2rem', color: '#b3294e' }}> {slug}  </h3>
          <p style={{ fontFamily: 'Marianne,Arial,sans-serif', fontWeight: 'bold', fontSize: '18px' }}>{titreAr}</p>
          <p style={{ fontFamily: 'Marianne,Arial,sans-serif' }}>{titre}</p>
        </div>
        <Button variant="contained" style={{ backgroundColor: '#4829B2' }} onClick={()=> { history.push('/Modules')}} color="primary">
          EN SAVOIR PLUS
        </Button>

      </Paper>
    </Fragment>
  );
}



export default withStyles(styles, { withTheme: true })(ModuleCard);
