import axiosinstance from './instance';
import {apiUrl} from './instance';
export function login(formBody){
    return    fetch(apiUrl+'/api/login', {
                    method: 'POST',
                    body: formBody,
                    headers: {
                        //Header Defination
                        'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    },
                })
}
//prb with cors i dont know why 
export  async function loginWithAxios(user){
    const data = await axiosinstance.post(apiUrl+"/api/login",user,{
        headers: {
            "Content-Type": " application/json"
        }
    });
    return data
}
export  async function RegisterWithAxios(user){
    const data = await axiosinstance.post(apiUrl+"/api/register",user,{
        headers: {
            "Content-Type": " application/json"
        }
    });
    return data
}
export function logout(){
    return    axiosinstance.post("/api/logout")
}